  height: 15px;
  width: 60%;
  border: 1px solid gray;
  border-radius: 5px;
  justify-self: center;
}

.persons {
  grid-area: 1 / 3 / -1 / span 2;
  grid-template-columns: repeat(2, 1fr);
  font-weight: 600;
  display: grid;
  overflow: scroll;
}

.person span {
  opacity: .7;
  font-size: .7em;
}

.person ul {
  margin-top: 2vh;
}

.person li {
  font-size: 1em;
}

.title {
